*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Space Grotesk", sans-serif;
}

.container {
  margin: 0 auto;
  width: 25rem;
  margin-top: 6rem;
}

.booklist {
  text-align: center;
}

table {
  table-layout: fixed;
  border: 1px solid;
  width: 100%;
  border-collapse: collapse;
}
table > tbody {
  background-color: gray;
}